.galerie-container {
  position: relative;
  margin-bottom: 30px;
  height: 415px;
  max-height: 415px;
}

.galerie-container .galerie {
  margin: auto;
}

.galerie-container .galerie img.img-galerie {
  width: 100%;
  height: 415px;
  object-fit: cover;
  border-radius: 25px;
}
.galerie-container .galerie .img-position {
  position: absolute;
  font-size: 18px;
  line-height: 142.6%;
  text-align: center;
  color: #ffffff;
  right: 49.11%;
  top: 86.19%;
  bottom: 6.01%;
}

.galerie-container .btn {
  background: none;
  border: none;
  color: #ffffff;
  font-size: 60px;
  position: absolute;
  z-index: 10;
  top: calc(50% - 30px);
}
.galerie-container .next {
  right: 0;
}

.chevron-visible {
  display: block;
}
.chevron-hidden {
  display: none;
}

@media all and (max-width: 768px) {
  .galerie-container {
    height: 255px;
    max-height: 255px;
  }

  .galerie-container .galerie img.img-galerie {
    height: 255px;
  }

  .galerie-container .btn {
    font-size: 30px;
    top: calc(50% - 15px);
  }
  .galerie-container .galerie .img-position {
    display: none;
  }
}

